import React , { useState, useEffect } from 'react'
import "../../Styles/pages/orderSuccess.css"
import { Link } from 'gatsby'

function OrderSuccess() {
  const [checkmarkVisible, setCheckmarkVisible] = useState(false)

  useEffect(() => {
    // Trigger checkmark animation after component mounts
    const timer = setTimeout(() => {
      setCheckmarkVisible(true)
    }, 300)

    return () => clearTimeout(timer)
  }, [])

  return (
<>
 <Link to="/orderfailed" className="toggle-success-failed">
            Toggle Success and Failed
        </Link>
    <div className="thank-you-page">
      {/* Header */}
      <header className="order-success-header">
        <div className="container">
          <h1 className="page-title">Thank You</h1>
          <nav className="breadcrumb">
            <a href="#" className="breadcrumb-link">Home</a>
            <span className="breadcrumb-separator">›</span>
            <span className="breadcrumb-current">Thank You</span>
          </nav>
        </div>
      </header>

      {/* Main Content */}
      <main className="main-content">
     
          <div className="success-content">
            {/* Animated SVG Checkmark */}
            <div className={`checkmark-container ${checkmarkVisible ? 'animate' : ''}`}>
              <div className="checkmark-circle">
                <svg className="checkmark-svg" viewBox="0 0 52 52">
                  <circle 
                    className="checkmark-circle-svg" 
                    cx="26" 
                    cy="26" 
                    r="25" 
                    fill="none"
                  />
                  <path 
                    className="checkmark-check" 
                    fill="none" 
                    d="m14.1 27.2l7.1 7.2 16.7-16.8"
                  />
                </svg>
              </div>
            </div>

            {/* Success Message */}
            <h2 className="success-title">Thanks For your Order</h2>
            <p className="success-description">
              Sorry, we couldn't find the page you where looking for. We suggest that<br />
              you return to homepage.
            </p>

            {/* Action Button */}
            <button className="homepage-button">
              <span className="button-icon">←</span>
              Go To Homepage
            </button>

            {/* Footer Text */}
            <p className="footer-text-order">
              Let's track your order or <a href="#" className="contact-link">Contact Us</a>
            </p>
          </div>
      </main>
     
    </div>
     
</>
  )
}



 
export default OrderSuccess